@import url(https://fonts.googleapis.com/css?family=Arimo&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:300&display=swap);
.Container {
    position: relative;
    width:100%;
    height: auto;
}

/* Style the video: 100% width and height to cover the entire window */
.Video {
    width: 100%;
}

/* Add some content at the bottom of the video/page */
.Content {
    position: absolute;
    bottom: 16px;
    color: white;
    width: 100%;    
}




.deviceCard, .deviceCard-active{
    background-color: white;
    font-family: 'Lato', sans-serif;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width:100%;
    height: 15vh;
    padding-top: 15px;
    border-left: 1px solid #eee;
    
}

.deviceName, .deviceImg img{
    margin: 0px 5px;
}

.deviceImg img{
    height: 50px;
}

.deviceName {
    font-size: 0.8em; 
     
}

.divider {
    background-color: #e50914;
    width: 100%;
    height: 2.5vh;
}

@media(min-width:800px) {
    .option{
        width: 200px
    }
}

@media(max-width:800px){
    .deviceName {
        font-size: 0.7em; 
     
    }

    .option,.deviceCard,.deviceCard-active{
        min-width: 100px;
    }
}

@media(max-width:600px){
   
    .deviceImg img {
        height: 42px;
    }

}




.description-section {
    display : flex;
    flex-direction:row;
    height: 55vh;
    background-image: url(https://d1qmdf3vop2l07.cloudfront.net/radioactive-shoelace.cloudvent.net/compressed/_min_/efbc3eac19a10b943d5360eb7825c08f.jpg)
    
}
.descriptions {
    font-family: Poppins, sans-serif;
    width: 580px;
}

.description-text{
    margin-top: 50px;
    margin-left: 100px;
    color: whitesmoke;
}

.description-text h3 {
    font-size: 37px;
    text-transform: uppercase;
    border-left: 2px solid #e50914;
    padding-left: 27px;
}

.description-text p {
    padding-top: 0px;
    font-size: 13px;
    line-height: 1.5;
}

@media(max-width:800px) {
    .description-text {
        margin-left: 62px;
    }
}

@media(max-width:750px) {
    .description-text h3 {
        font-size: 32px;
        padding-left: 15px;
        margin-left: -5px;

    }
}

@media(max-width:600px) {
    .description-text {
        margin-left: 20px;
    }
    .description-text h3 {
        font-size: 29px;
        padding-left: 10px;

    }

    .description-text p {
        font-size: 11px;
        line-height: 1;
    }

}

@media(max-width:500px) {
    .description-text {
        margin-left: 22px;
    }

}


.devices {
    padding: 0;
    display: flex;
    overflow: auto;
}

